<template>
  <div>

    <el-table :data="customerList" width="100%">
      <el-table-column prop="booOrderNum" label="Order number"></el-table-column>

      <el-table-column prop="boobAddress" label="Departure"></el-table-column>
      <el-table-column prop="booAAddress" label="Destination"></el-table-column>
      <el-table-column prop="boobTime" label="Departure time"></el-table-column>
      <el-table-column prop="reserve2" label="Total amount"></el-table-column>
      <el-table-column prop="create_time" label="Creation time"></el-table-column>
      <el-table-column prop="flagPay" label="Order status">
        <template slot-scope="scope">
          <el-tag type="danger" v-if="scope.row.flagPay==0">Unpaied</el-tag>
          <el-tag type="success" v-if="scope.row.flagPay==1">Paied</el-tag>
          <el-tag type="info" v-if="scope.row.flagPay==2">Canceled</el-tag>
        </template>

      </el-table-column>
      <el-table-column label="View the order">
        <template slot-scope="scope">
          <el-button size="mini" @click="editcustomer(scope.$index,scope.row)">View</el-button>
        </template>
      </el-table-column>
      <el-table-column label="Customer operation">
        <template slot-scope="scope" width="180">
          <el-button type="danger" size="mini" @click="deletecustomer(scope.$index,scope.row)" v-if="scope.row.flagPay==0">delete</el-button>
          <el-button type="success" size="mini" @click="pay(scope.$index,scope.row)" v-if="scope.row.flagPay==0">pay</el-button>
          <el-button type="primary" size="mini" @click="returnticket(scope.$index,scope.row)" v-if="scope.row.flagPay==1">Refund</el-button>
        </template>
      </el-table-column>
    </el-table>
    <el-pagination style="margin-top: 10px" background layout="prev, pager, next" :current-page.sync="pageInfo.pageNum" :page-count="pageInfo.pages" :page-size="pageInfo.pageSize" :total="pageInfo.total" @current-change="this.findList">
    </el-pagination>
    <el-dialog :visible.sync="editDialogVisible" title="Order details">
      <el-form :model="customer" ref="customerForm" label-width="100px" style="width: 500px;" size="small ">
        <el-form-item label="Order Id" prop="booOrderNum">
          <el-input v-model="customer.booOrderNum" :disabled="true" />
        </el-form-item>
        <el-form-item label="Ticket Id" prop="booNo">
          <el-input v-model="customer.booNo" :disabled="true" />
        </el-form-item>
        <el-form-item label="Scheduled Time" prop="boobTime">
          <el-input v-model="customer.boobTime" :disabled="true" />
        </el-form-item>
        <el-form-item label="Order State" prop="bookFlag">
          <el-input v-model="customer.bookFlag" :disabled="true" />
        </el-form-item>
        <el-divider content-position="left">Booking Person</el-divider>
        <div v-for=" pr in customer.persions" :key="pr.cusAccount">
          <el-form-item label="Name">
            <el-input v-model="pr.cusAccount" style="float: left;width: 400px" disabled />
          </el-form-item>
          <el-form-item label="Phone">
            <el-input v-model="pr.cusTelNumber" style="float: left;width: 400px" disabled />
          </el-form-item>
          <el-form-item label="Id number">
            <el-input v-model="pr.cusId" style="float: left;width: 400px" disabled />
          </el-form-item>
          <el-form-item label="Aircraft space">
            <el-input v-model="pr.booBerth" style="float:left;width: 80px" disabled />
          </el-form-item>
          <el-form-item label="Price">
            <el-input v-model="pr.fliYfare" style="float:left;width: 80px" disabled />
          </el-form-item>
          <hr>
        </div>

        <el-form-item>
          <el-button @click="editDialogVisible=false">close</el-button>
        </el-form-item>
      </el-form>
    </el-dialog>
    <el-dialog title="Pay now" :visible.sync="dialogVisible1" width="30%" :before-close="handleClose">
      <el-form ref="form" :model="form" label-width="80px">
        <el-form-item label="Amount paid">
          <el-input v-model="paynum" disabled></el-input>
        </el-form-item>
        <el-form-item label="Payment password">
          <el-input v-model="payPwd"></el-input>
        </el-form-item>
        <el-form-item label="支付方式">
          <el-select v-model="payType" placeholder="Method of payment">
            <el-option v-for="item in payoptions" :key="item.username" :label="item.name" :value="item.username">
            </el-option>
          </el-select>

        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
            <el-button @click="dialogVisible1 = false">Cancel</el-button>
            <el-button type="primary" @click="onpay">Confirm</el-button>
        </span>
    </el-dialog>
  </div>
</template>

<script>
export default {
    name: "vip",
    data() {
        return {
            customer: {},
            payPwd: '',
            payType: '',
            form: {},
            paynum: 0,
            rowForm: {},
            dialogVisible1: false,
            payoptions: [],
            pageInfo: {
                pageNum: 1,

                /*当前是那一页*/
                pageSize: 10,
                /*每页展示多少条*/
                pages: 10,
                /*总共有多少页*/
                total: 100 /*总共有多少条数据*/
            },
            customerList: [],
            editDialogVisible: false,
        };
    },
    created() {
        this.findList()
    },
    methods: {
        returnticket(index, row) {
            this.$confirm('This operation is a refund operation. Do you want to continue?', '提示', {
                confirmButtonText: 'Confirm',
                cancelButtonText: 'Cancel',
                type: 'warning'
            }).then(() => {
                let user = JSON.parse(window.localStorage.getItem("user"))
                axios.post("/api/payerLink/exitMoney", {
                    name: user.username,
                    payNum: parseInt(row.reserve2),
                    reserve2: row.reserve3
                }).then(resp => {
                        if (resp.data.success) {
                            this.$message({
                                type: 'success',
                                message: 'Successful refund'

                            });
                        
                                   this.rowForm=row
                                    this.rowForm.flagPay = 2;
                        this.saveOrder()

                        } else {
                                this.$message({
                            type: 'erro',
                            message: 'Refund failed'

                        });
                }
            })

        }).catch(() => {
        this.$message({
            type: 'info',
            message: 'Undeleted'
        });
    })
},
handleClose() {
        this.dialogVisible1 = false
    },
    onpay() {
        let user = JSON.parse(window.localStorage.getItem("user"))
        if (this.payPwd == '') {
            this.$message({
                type: 'error',
                message: 'Please enter the payment password'
            })
            return
        } else {
            if (this.payPwd == user.payPwd) {
                axios.post("/api/payerLink/pay", {
                    name: user.username,
                    payNum: parseInt(this.paynum),
                    reserve2: this.payType
                }).then(resp => {
                    if (resp.data.success) {
                        this.$message({
                            type: 'success',
                            message: 'Payment successful!'
                        })
                        this.rowForm.flagPay = 1;
                            this.rowForm.reserve3 = this.payType
                        this.saveOrder()
                        this.dialogVisible1 = false
                    } else {
                        this.$message({
                            type: 'erro',
                            message: resp.data.message
                        })
                    }
                })
            } else {
                this.$message({
                    type: 'error',
                    message: 'Payment password error'
                })
            }
        }

    },
    saveOrder() {

        let user = JSON.parse(window.localStorage.getItem("user"))
        if (user == null) {
            this.$message({
                showClose: true,
                message: 'Please log in first',
                type: 'error'
            });
        }
        this.rowForm.reserve1 = user.username;
    
        axios.post("/api/book/update", this.rowForm).then(response => {

            if (response.data.success) {
                this.$message({
                    title: '提示',
                    message: "Submitted",
                    type: 'success'
                })
            } else {
                this.$message({
                    type: 'error',
                    message: 'Submit failed'
                })
            }
            this.clear();
            this.paymentDialogVisible = false;
        })
    },
    pay(index, row) {
        this.paynum = row.reserve2
        this.rowForm = row
        axios.get("/api/payerLink/list", {}).then(res => {
            this.payoptions = res.data.data
            this.dialogVisible1 = true
        })
    },
    deletecustomer(index, row) {
        this.$confirm('This operation will permanently delete the administrator. Do you want to continue?', '提示', {
            confirmButtonText: 'Yes',
            cancelButtonText: 'cancel',
            type: 'warning'
        }).then(() => {
            //发生请求去删除对应的数据
            axios.delete("/api/book/delete?id=" + row.id).then(resp => {
                if (resp.data.success) {
                    this.$message({
                        type: 'success',
                        message: 'Deleted'
                    });
                } else {
                    this.$message({
                        type: 'error',
                        message: 'Delete failed'
                    });
                }
                //重新获取表格数据
                this.findList();
            })

        }).catch(() => {
            this.$message({
                type: 'info',
                message: 'undeleted'
            });
        });

    },
    findList() {
        let user = JSON.parse(window.localStorage.getItem("user"))
        axios.post("/api/book/page", {
            pageNum: this.pageInfo.pageNum,
            pageSize: this.pageInfo.pageSize,
            search: user.username,
            air:''
        }).then(resp => {
            //date:{list:[],pageSize,pageNum,pages,total}
            // console.log(resp.data);
            this.pageInfo.pageSize = this.pageInfo.pageSize;
            this.pageInfo.pageNum = this.pageInfo.pageNum;
            this.pageInfo.total = resp.data.total;
            this.pageInfo.pages = resp.data.total;
            //将数据赋值给customerList表格数据
            this.customerList = resp.data.data;
        })
    },
    editcustomer(index, row) {
        //记录下原来的用户名
        axios.get("/api/book/info?id=" + row.id).then(resp => {

            this.customer = resp.data.data;

        })
        //弹出一个可以修改输入用户信息的表单窗口
        this.editDialogVisible = true;
        //克隆row

    },
}
};
</script>

<style scoped>
.el-carousel__item {
  background-image: url("../../views/children/image/1.jpg");
}

.el-carousel__item:nth-last-child(3) {
  background-image: url("../../views/children/image/2.jpg");
}

.el-carousel__item:nth-last-child(2) {
  background-image: url("../../views/children/image/3.jpg");
}

